/* sticky 버튼 */
.btn_sticky{
	z-index:600;
	width:100%;
	bottom:0;
	left:0;
	position:fixed;
	display:flex;
	flex-flow: row nowrap;
	.isStickyOff & {
		position: static;
		margin: 0 0 60px;
	}
	button {
		flex: 1;
	}
	button:not(:last-child) {
		border-right:1px solid rgba($COL_White , 0.15);
	}
	.icon {
		width:56px;
		height:56px;
		button {
			display: block;
			width: 100%;
			height: 56px;
			background-color:$COL_White;
			border-top:1px solid map-get($COL_Line_01, "bg_light");
			border-bottom:1px solid map-get($COL_Line_01, "bg_light");
			@include Mix_Size_0;
			&.btn_refresh {
				background-color:map-get($COL_Box_02,'bg_dark');
				border:none;
			}
		}
		.chk_wish + label {
			background-color:$COL_White;
			border-top:1px solid map-get($COL_Line_01, "bg_light");
			border-bottom:1px solid map-get($COL_Line_01, "bg_light");
		}
	}

	.btn_refresh{ // 검색 조건 초기화
		background-color:map-get($COL_Box_02,'bg_dark');
		border-color:rgba($COL_White,0.15); 		
		&::before{
			content:'';
			display:block;
			width:24px;
			height:24px;
			margin:0 auto;
			@include Mix_Display_Bg;
			background-position:-71px -105px;
		}				
		&02 {
			background-color:$COL_White;
			border-color:map-get($COL_Line_01, "bg_light");
			border-width: 1px 0; 
			&::before {
				content:'';
				display:block;
				width:24px;
				height:24px;
				margin:0 auto;
				@include Mix_Display_Bg;	
				background-position:-42px -105px;
			}
		}
	}	

	.order_price{ // 결제하기, 남은 결제금액
		width:100%;
		height:60px;
		padding:20px 21px;
		// background:$COL_Gray05;
		display:flex;
		justify-content:space-between;
		.tit{
			// color:$COL_Gray08;
			@include Mix_Font('Head-R', 16, 20);
		}
		.price{
			// color:$COL_Black;
			@include Mix_Font('Head-R', 16, 20);
			em{
				@include Mix_Font('Head-M', 18, 20);
			}
		}
	}	
	.btn_big01 {
		border-left:1px solid rgba($COL_White,0.15);
	}
	.btn_big02 {
		border:1px solid map-get($COL_Line_01,'bg_light');
		&:first-child {
			border-width: 1px 0;
		}
	}
}

