.cpo_tooltip{
	position:relative;
	display:inline-block;
	margin:1px 0 0 3px;
	.open{
		display:inline-block;
		width:16px;
		height:16px;
		@include Mix_Size_0;
		@include Mix_Form_Bg;
		background-position:0 -53px;
	}
	&.on{
		.cont{
			display:block;
		}
	}
	.cont{
		display:none;
		position:absolute;
		top:29px;
		left:1px;
		z-index:999;
		width:240px;
		padding:20px 20px 21px;
		border:1px solid map-get($COL_Line_01, "bg_light"); 
		margin-left:-20px;
		background:$COL_White;
		.entry_carbase &{// 내차팔기 '외판스크래치 툴팁
			.swiper-slide{
				.img{
					margin-bottom:26px;
				}
			}
			.swiper-pagination{
				bottom:unset !important;
				top:110px;
			}
		}
		> .text{
			color:$COL_Black;
			@include Mix_Font('Txt-R', 12, 19);
			word-break:keep-all;
			~ .caution {
				margin-top: 10px;
			}
		}
		
		.img{
			margin-bottom:10px;
			img{
				width:100%;
			}
		}
		.caution{// 안내 문구만 있는 경우
			color: $COL_Black;
			&:before { background: $COL_Black; }
			li { color: $COL_Black; }
			li ~ li { margin-top: 10px;	}
			li:before { background: $COL_Black; }
		}
		.close{
			@include Pos($p:absolute,$t:4px,$r:4px);
			@include Mix_Del_Icon(9, 1.2, "666666", 16);
			@include Mix_Size_0;
			display: flex;
			justify-content: center;
			align-items: center;
		}
	}
}
.bg_dark .toggle .btn_more:after {@include Icon_Arr(1.5, 'BBBBBB');}

.accordion{
	//아코디언 추가
	.tit_btn {
		position:relative;
		&::after {
			content:'';
			position:absolute;
			top:50%;
			right: 0;
			transform: translateY(-50%);
			transition: all .3s ease-in;
			@include Icon_Arr(1.5, '8F8F8F');
		}
	}
	.active {
		.tit_btn {
			&:after {
				transform: rotate(-180deg) translateY(50%);
			}
		}
	}
}

.toggle {
	//더보기
	.btn_more{  
		position: relative;
		display: block;
		width: 100%;
		color: map-get($COL_Base_02,'bg_light'); 
		text-align: center;
		&:after {
			display: inline-block;
			content: '';
			margin: 5px;
			@include Icon_Arr(1.5, '727272');
			transition: all .3s ease-in;
			vertical-align: top;
		}
	}
	.on:not(.srch_box),
	&.on{
		.btn_more{
			&:after {
				transform: rotate(-180deg);
			}
		}
	}

	// 아코디언 아닌 토글 
	.tit_btn {
		position:relative;
		&::after {
			content:'';
			position:absolute;
			top:50%;
			right: 0;
			transform: translateY(-50%);
			transition: all .3s ease-in;
			@include Icon_Arr(1.5, '8f8f8f');
		}
		.bg_dark & {
			&::after {
				@include Icon_Arr(1.5, '828282');
			}
		}
	}
	.on {
		.tit_btn {
			&:after {
				transform: rotate(-180deg) translateY(50%);
			}
		}
	}
	.qa_wrap {
        @include Mix_faq;
        text-align: center;
    }
}

