// swich
.switch{
	position:relative;
	width:50px;
	height:30px;
	input{
		position:absolute;
		width:100%;
		height:100%;
		left:0;
		top:0;
		display:block;
		opacity:0;
		cursor:pointer;
		z-index:2;
		&:checked + .circle{
			left: calc(100% - 28px);
		}
		&:checked + .circle + .background{
			background-color: map-get($COL_Base_05, "bg_light");
		}
	}
	.circle{
		width: 26px;
		height: 26px;
		background-color:$COL_White;
		border-radius:200%;
		transition:all 0.2s ease-out;
		position:absolute;
		left: 2px;
		top: 2px;
		@include Boxshadow(1px, 4px, .2);
	}
	.background{
		width:100%;
		height:100%;
		background-color: map-get($COL_Line_01, "bg_light");
		border-radius: 15px;
		transition:background 0.2s ease-out;
	}
}


